.videoTab {
  padding: 10px;
}
.videoTab-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 2%;
}
.videoTab-icons {
  margin: 1% 0;
  justify-content: flex-start;
  display: flex;
  align-items: center;
  margin-left: 30px;
}
.faveIcon {
  margin-right: 20px;
}
.faveIcon:hover {
  cursor: pointer;
}
.shareIcon {
  margin-right: 20px;
}
