.header {
  width: 100%;
  height: 50px;
  background-color: black;
  color: antiquewhite;
  font-size: 24px;
  position: fixed;
  font-weight: 600;
  padding: 5px;
  display: flex;
  align-items: center;
  top: 0;
  /* margin-left: 380px; */
  margin-bottom: 5px;
}
.header-tag {
  padding-left: 400px;
}
.header-link {
  margin-left: 40px;
  font-size: 14px;
  display: none;
}
.header-menu {
  display: none;
}

@media screen and (max-width: 1500px) {
  .header-link {
    display: flex;
  }
}
@media screen and (max-width: 1900px) {
  .header-tag {
    padding-left: 20px;
    padding-right: 80px;
    left: 0;
  }
  .header-link {
    font-size: 24px;
    display: flex;
  }
}
@media screen and (max-width: 1300px) {
  .header-link {
    font-size: 14px;
  }
  .header-tag {
    font-size: 16px;
  }
}
@media screen and (max-width: 900px) {
  .header-link {
    display: none;
  }
  .header-tag {
    font-size: 16px;
    width: 120px;
  }
  .header-menu {
    display: contents;
  }
}
