.header {
  width: 100%;
  height: 50px;
  background-color: black;
  color: antiquewhite;
  font-size: 24px;
  position: fixed;
  font-weight: 600;
  padding: 5px;
  display: flex;
  align-items: center;
  top: 0;
  margin-left: 20px;
  margin-bottom: 5px;
}
.noneSelected-text {
  font-size: 70px;
  font-weight: bolder;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 70px;
  padding: 50px 100px;
}
.container > p {
  margin-left: 5%;
}
.container > img {
  margin-left: 5%;
}
