.main {
  display: flex;
  align-items: center;
  margin-left: 320px;
  flex: 0.7;
}

.scrollGroup {
  margin-top: 70px;
  padding: 50px;
}
.main-container {
  display: flex;
  flex-direction: row;
  padding: 0px 50px;
}
.resourcesSection {
  padding: 50px;
  margin-top: 70px;
  margin-right: 40px;
  display: flex;
  flex-direction: column;
  width: 700px;
  position: fixed;
  left: 0;
  margin-left: 55%;
}
.resourcesSection > h2 {
  font-size: 40px;
  font-weight: 700;
}
.resourcesSection > a {
  font-size: 26px;
  font-weight: 500;
  text-decoration: none;
  margin-bottom: 20px;
  color: black;
}
.resourcesSection > div {
  margin-top: 20%;
  font-weight: 700;
}

@media screen and (max-width: 1900px) {
  .main {
    margin-left: 0px;
  }
}

@media screen and (max-width: 1350px) {
  .resourcesSection {
    display: none;
  }
  /* .main {
    margin-left: 15%;
  } */
}
