.sidebarOption {
  width: 380px;
  height: 102px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: black;
  margin-top: 7px;
}
.sidebarOption:hover {
  opacity: 0.8;
}

@media screen and (max-height: 950px) {
  .sidebarOption {
    height: 80px;
  }
}
@media screen and (max-height: 850px) {
  .sidebarOption {
    height: 60px;
  }
}
@media screen and (max-height: 700px) {
  .sidebarOption {
    height: 40px;
  }
}
