.drawer {
  display: flex;
  flex-direction: column;
  flex: 0.3;
  background-color: rgb(28, 34, 49);
  width: 380px;
  height: 100%;
  position: fixed;
  overflow: hidden;
}
.drawer-profile {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 200px;
  width: 380px;
  background-color: rgb(247, 215, 215);
  padding: 50px 0px;
}
.user-icon {
  margin-bottom: 5%;
}
.profile-email {
  font-weight: 700;
  font-size: 22px;
  margin-bottom: 5%;
}
.sidebar-link {
  text-decoration: none !important;
  cursor: pointer;
  color: black;
}
.sidebar-link:hover {
  text-decoration: none;
}
